import React from "react";

export default function Press({ title, img }) {
  return (
    <section className="press">
      <div className="container">
        <div className="press__inner">
          <h3 className="press__title heading-3">{title}</h3>
          <img src={img} alt="press img" />
        </div>
      </div>
    </section>
  );
}
