import React from "react";

function Card({ title, subtitle, link }) {
  return (
    <a className="card" href={link}>
      <div className="card__content">
        <h2 className="card__title">{title}</h2>
        <h4 className="card__subtitle">{subtitle}</h4>
      </div>
    </a>
  );
}
export default Card;
