import React from "react";

const GetStarted = ({ title, text, btnText }) => {
  return (
    <section className="started">
      <div className="started__inner">
        <h2 className="heading-2">{title}</h2>
        <p>{text}</p>
        <a className="btn-primary" href="/">
          {btnText}
        </a>
      </div>
    </section>
  );
};
export default GetStarted;
