import React from 'react';
function Fundraise() {
    return (
        <section className="fundraise">
            <div className="fundraise__inner">
                <div className="fundraise__content">
                    <h2 className="heading-2">Fundraise with custom shirts</h2>
                    <p>
                        Easily raise money for your community by selling custom shirts. Perfect for individuals, groups,
                        causes and non-profits. No inventory needed — we do all the work!
                    </p>
                    <a href="/" className="btn-primary">
                        Start a fundraiser
                    </a>
                </div>

                <div className="fundraise__bg"></div>
            </div>
        </section>
    );
}
export default Fundraise;