import React from "react";
import sell_bg from "../../img/sell_online_backgorund.jpg";

function SellOnline() {
  return (
    <section className="sell">
      <div className="sell__content">
        <h2 className="sell__heading heading-2">
          Sell shirts online through campaign
        </h2>
        <p className="sell__text">
          Sell custom products online by creating your own campaign page where
          anyone can check out.
        </p>
        <p className="sell__text">
          When your campaign ends, we ship products directly to your buyers and
          send you the profits.
        </p>
        <a href="/" className="btn-primary sell__btn">
          sell shirts online
        </a>
      </div>
      <div className="sell__bg-img">
        <img src={sell_bg} alt="sell online bg img" />
      </div>
    </section>
  );
}
export default SellOnline;
