import React from "react";
import Cart from "../../components/Card";
import Footer from "../../components/Footer";
import GetStarted from "../../components/GetStarted";
import Header from "../../components/Header";
import OpenStore from "../../components/OpenStore";
import Press from "../../components/Press";
import Product from "../../components/Product";
import { images } from "../../img/images";
import search from "../../img/search.svg";
import Fundraise from "./Fundraise";
import OrderShirt from "./OrderShirt";
import SellOnline from "./SellOnline";
import Support from "./Support";
import TrustRate from "./TrustRate";
import trusted_1 from '../../img/trusted-1.jpg';
import trusted_2 from '../../img/trusted-2.jpg';
import trusted_3 from '../../img/trusted-3@2x.jpg';

export const HomePage = () => {
  const getHero = () => (
    <section className="hero">
      <div className="hero__content">
        <h2 className="hero__slogan">Design your next favorite custom shirt</h2>
        <a className="btn-primary" href="/">
          get started
        </a>
      </div>
      <div className="container">
        <div className="hero__search">
          <img src={search} alt="search icon" />
          <div className="input-field">
            <input className="input-field__input" type="text" id="search" placeholder="Search all campaigns" />
            <label className="input-field__label" htmlFor="search">
              Search all campaigns
            </label>
          </div>
          <a href="/" className="btn-frame">
            search
          </a>
        </div>
      </div>
    </section>
  );
  const getPicks = () => (
    <section className="picks">
      <div className="container">
        <div className="picks__inner">
          <h2 className="heading-2">Bonfire is the easiest way to design, sell and order premium custom shirts.</h2>
          <h3 className="picks__subtitle">bonfire staff picks</h3>
          <ul className="picks__list">
            <li className="picks__item">
              <Product />
            </li>
            <li className="picks__item">
              <Product />
            </li>
            <li className="picks__item">
              <Product />
            </li>
          </ul>
          <a href="/" className="picks__btn btn-frame">
            see all
          </a>
        </div>
      </div>
    </section>
  );
  const getCatalog = () => (
    <section className="catalog">
      <div className="container">
        <div className="catalog__inner">
          <Cart title="Personal fundraising" subtitle="adoption" link="/" />
          <Cart title="Personal fundraising" subtitle="adoption" link="/" />
        </div>
      </div>
    </section>
  );
  return (
    <main className="homepage">
      <Header />
      {getHero()}
      {getPicks()}
      <SellOnline />
      <OpenStore
        title="Open a free online store"
        text="Make it easy for users to browse all of the custom shirts and
        apparel you’ve designed by creating an online store. They’re free to
        make and simple to tailor to your brand."
      />
      <OrderShirt />
      <Fundraise />
      <TrustRate hasTitle={true} img1={trusted_1} img2={trusted_2} img3={trusted_3} />
      <GetStarted title="Ready to start?" text="You’re only one step away from designing your next favorite shirt." btnText="Get started" />
      <Support />
      <Press title="Campaigns in the press" img={images.press} />
      {getCatalog()}
      <Footer />
    </main>
  );
};
