import React from 'react';
import support_bg from '../../img/support@2x.jpg';

export default function Support() {
    return (
        <section className="support">
            <div className="support__container">
                <div className="support__content">
                    <h2 className="heading-2">Responsive, and caring customer support.</h2>
                    <p>
                        From the moment you set out to create a custom shirt, our dedicated customer happiness and
                        account management teams have your back every step of the way.
                    </p>
                </div>
                <img className="support__bg" src={support_bg} alt="support backgorund img"></img>
            </div>
        </section>
    );
}
