import React from 'react';


function TrustRate({ img1, img2, img3, hasTitle }) {
    return (
        <section className="trate">
            <div className="container">
                <div className="trate__inner">
                    {
                        hasTitle &&
                        <h2 className="trate__title">Trusted by thousands of organizations, businesses and individuals.</h2>
                    }
                    <ul className="trate__list">
                        <li className="trate__item">
                            <img src={img1} alt="trusted" className="trate__img" />
                        </li>
                        <li className="trate__item">
                            <img src={img2} alt="trusted" className="trate__img" />
                        </li>
                        <li className="trate__item">
                            <img src={img3} alt="trusted" className="trate__img" />
                        </li>
                    </ul>
                </div>
            </div>
        </section>
    );
}
export default TrustRate;