import React from 'react';
import pick_img from '../img/pick_img.jpg';

function Product() {
    return (
        <a className="product" href='/'>
            <img className="product__img" src={pick_img} alt="pick img" />
            <h3 className="product__name">Mt Whitney Apparel</h3>
        </a>
    );
}
export default Product;