import React from "react";
import { images } from "../img/images";

const OpenStore = ({ title, text }) => {
  return (
    <section className="store">
      <div className="store__inner">
        <div className="store__content">
          <h2 className="heading-2">{title}</h2>
          <p className="store__text">{text}</p>
          <a href="/" className="store__btn btn-frame btn-frame--light">
            <img src={images.store} alt="store icon" />
            <span>open your store</span>
          </a>
        </div>
        <div className="store__bg" />
      </div>
    </section>
  );
};
export default OpenStore;
